.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

button {
  text-transform: uppercase;
  letter-spacing: 1.1px;
  color: #272727;
  background-color: #f5f5f5;
  border: none;
  border-radius: 5px;
  padding: 12px 40px;
  font-size: 16px;
  font-weight: 500;
  transition: background-color .25s ease-in-out, box-shadow 50ms ease-in-out;
  box-shadow: 1px .3px 2px .3px #313131;
}

button + button {
  margin-left: 10px;
}

button:hover {
  background-color: #ddd;
  box-shadow: 1px .3px 2px .3px #313131;
}

button:active {
  background-color: #9ea2a5;
  box-shadow: inset .3px 2px -.3px #313131;
}

button:disabled {
  background-color: #727374;
  box-shadow: inset 1px 1px 2px .3px #313131;
}

